'use client';

import React, { useState } from 'react';
import { supabase } from '@/lib/supabase';
import { useRouter } from 'next/navigation';
import { ShieldCheck, ArrowRight, Loader2, User, Mail, Lock, Sparkles, Globe } from 'lucide-react';
import Link from 'next/link';

export default function RegisterPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [fullName, setFullName] = useState('');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const router = useRouter();

  // Logika SignUp dengan Metadata agar Trigger Supabase Anda Berhasil
  const handleRegister = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const { data: authData, error: authError } = await supabase.auth.signUp({
        email,
        password,
        options: {
          // Metadata 'full_name' wajib ada agar masuk ke tabel 'profiles' Anda
          data: { full_name: fullName }
        }
      });

      if (authError) throw authError;
      
      if (authData.user) {
        router.push('/dashboard');
      }
    } catch (err: any) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-[#FDFCFB] flex flex-col md:flex-row font-sans text-black">
      
      {/* BAGIAN KIRI: VISUAL BRANDING (ELITE DESIGN) */}
      <div className="hidden md:flex md:w-1/2 bg-black p-20 flex-col justify-between relative overflow-hidden">
        <div className="absolute top-0 right-0 w-96 h-96 bg-[#D4AF37]/10 blur-[120px] rounded-full -mr-48 -mt-48"></div>
        <div className="absolute bottom-0 left-0 w-64 h-64 bg-[#D4AF37]/5 blur-[100px] rounded-full -ml-32 -mb-32"></div>
        
        <div className="relative z-10 space-y-6">
          <div className="w-16 h-16 bg-[#D4AF37] rounded-2xl flex items-center justify-center shadow-2xl shadow-gold/20">
            <ShieldCheck className="text-black w-10 h-10" />
          </div>
          <h2 className="text-5xl font-serif italic text-white leading-tight">
            The Vanguard of <br />
            <span className="text-[#D4AF37]">Global Fiscal</span> Intelligence.
          </h2>
          <p className="text-gray-400 text-[16px] font-light leading-relaxed max-w-md italic">
            Join the elite circle of international professionals utilizing high-fidelity AI simulations for cross-border optimization.
          </p>
        </div>

        <div className="relative z-10 grid grid-cols-2 gap-8 border-t border-white/10 pt-12">
          <div className="space-y-2">
            <p className="text-[#D4AF37] font-serif italic text-2xl">99.9%</p>
            <p className="text-[10px] text-gray-500 font-black uppercase tracking-[0.2em]">Data Integrity</p>
          </div>
          <div className="space-y-2">
            <p className="text-[#D4AF37] font-serif italic text-2xl">10k+</p>
            <p className="text-[10px] text-gray-400 font-black uppercase tracking-[0.2em]">Treaties Simulated</p>
          </div>
        </div>
      </div>

      {/* BAGIAN KANAN: FORM REGISTER */}
      <div className="w-full md:w-1/2 flex items-center justify-center p-8 md:p-20">
        <div className="max-w-md w-full space-y-12">
          <div className="space-y-4">
            <div className="inline-flex items-center gap-2 px-4 py-1.5 rounded-full bg-black text-[#D4AF37] text-[12px] font-black uppercase tracking-[0.3em]">
              <Sparkles className="w-4 h-4" /> Initialize Access
            </div>
            <h1 className="text-4xl font-serif italic text-black leading-tight">Create Your <br/><span className="text-[#D4AF37]">Laboratory Profile.</span></h1>
          </div>

          <form onSubmit={handleRegister} className="space-y-8">
            {error && (
              <div className="p-4 bg-red-50 text-red-500 text-[13px] rounded-2xl border border-red-100 italic animate-in fade-in duration-300">
                {error}
              </div>
            )}

            <div className="space-y-6">
              {/* Input Nama Lengkap */}
              <div className="space-y-2 group">
                <label className="text-[12px] font-black uppercase tracking-widest text-gray-400 ml-2 group-focus-within:text-[#D4AF37] transition-colors">Full Name</label>
                <div className="relative">
                  <User className="absolute left-5 top-1/2 -translate-y-1/2 text-gray-300 w-5 h-5" />
                  <input 
                    type="text" 
                    required 
                    value={fullName} 
                    onChange={(e) => setFullName(e.target.value)} 
                    className="w-full bg-white border border-gray-100 rounded-2xl py-5 pl-14 pr-6 text-[16px] focus:outline-none focus:border-[#D4AF37] transition-all shadow-sm" 
                    placeholder="Enter your full name" 
                  />
                </div>
              </div>

              {/* Input Email */}
              <div className="space-y-2 group">
                <label className="text-[12px] font-black uppercase tracking-widest text-gray-400 ml-2 group-focus-within:text-[#D4AF37] transition-colors">Professional Email</label>
                <div className="relative">
                  <Mail className="absolute left-5 top-1/2 -translate-y-1/2 text-gray-300 w-5 h-5" />
                  <input 
                    type="email" 
                    required 
                    value={email} 
                    onChange={(e) => setEmail(e.target.value)} 
                    className="w-full bg-white border border-gray-100 rounded-2xl py-5 pl-14 pr-6 text-[16px] focus:outline-none focus:border-[#D4AF37] transition-all shadow-sm" 
                    placeholder="name@company.com" 
                  />
                </div>
              </div>

              {/* Input Password */}
              <div className="space-y-2 group">
                <label className="text-[12px] font-black uppercase tracking-widest text-gray-400 ml-2 group-focus-within:text-[#D4AF37] transition-colors">Secure Password</label>
                <div className="relative">
                  <Lock className="absolute left-5 top-1/2 -translate-y-1/2 text-gray-300 w-5 h-5" />
                  <input 
                    type="password" 
                    required 
                    value={password} 
                    onChange={(e) => setPassword(e.target.value)} 
                    className="w-full bg-white border border-gray-100 rounded-2xl py-5 pl-14 pr-6 text-[16px] focus:outline-none focus:border-[#D4AF37] transition-all shadow-sm" 
                    placeholder="••••••••" 
                  />
                </div>
              </div>
            </div>

            <button 
              type="submit" 
              disabled={loading} 
              className="w-full py-6 bg-black text-[#D4AF37] rounded-3xl text-[14px] font-black uppercase tracking-[0.2em] hover:bg-[#D4AF37] hover:text-black transition-all shadow-2xl shadow-gold/10 flex items-center justify-center gap-4 group"
            >
              {loading ? (
                <Loader2 className="animate-spin w-6 h-6" />
              ) : (
                <>Initialize Laboratory <ArrowRight className="group-hover:translate-x-2 transition-transform" size={20} /></>
              )}
            </button>
          </form>

          <div className="pt-8 border-t border-gray-50 text-center">
            <p className="text-[14px] text-gray-400 font-light">
              Already possess an elite account? <Link href="/auth/login" className="text-black font-bold hover:text-[#D4AF37] underline transition-colors">Sign In Here</Link>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}